@import "../../node_modules/bootstrap/scss/bootstrap-reboot.scss";

body {
    margin: 0;
    padding: 0;
    font-family: 'HelveticaNeueCyr';
    font-size: 16px;
    font-weight: normal;
    overflow-x: hidden; }

.container {
    padding: 0 20px; }

.section_header {
    font-size: 36px;
    line-height: 43px;
    font-weight: 500;
    color: #2E2E2E;
    margin: 0 auto; }

hr {
    border: none;
    margin: 0;
    height: 1px;
    background-color: #EAEAEA; }

.container {
    padding: 0 15px;
    width: 100%;
    max-width: 1200px;
    margin: auto; }


.row {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px; }

input[type="text"],
input[type="tel"] {
    width: 100%;
    padding: 15px 20px;
    border: 1px solid #C4C4C4;
    &:focus {
        outline: 0;
        box-shadow: 0 0 4px 0 #FF6600; } }

input[type="submit"] {
    margin: 40px auto 0;
    border: 1px solid #FF6600;
    background: transparent;
    font-size: 14px;
    color: #FF6600;
    padding: 15px 70px;
    transition: .4s;
    &:hover {
        transition: .4s;
        background-color: #FF6600;
        color: #ffffff; } }

input.error {
    border-bottom: 1px solid red; }

input.valid {
    border-bottom: 1px solid green; }

