.footer_menu_container, .footer_menu {
    display: flex;
    align-items: center;
    width: 100%;
    height: 130px; }

.footer_logo {
    margin-right: 100px;
    width: 192px; }

.footer_menu {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    a {
        font-size: 14px;
        line-height: 17px;
        color: #2E2E2E;
        text-decoration: none;
        font-size: 14px;
        display: block;
        line-height: 130px;
        text-transform: uppercase;
        transition: all .3s;
        &:hover {
            color: #FF6600;
            transition: all .3s; } } }

.copyright {
    border-top: 1px solid #EAEAEA; }

.copyright_text {
    font-size: 13px;
    line-height: 16px;
    color: #787878;
    margin: 20px 0; }
