#feedback-modal {
    width: 560px;
    max-width: calc(100% - 40px) !important;
    max-height: calc(100% - 40px);
    .iziModal-content {
        padding: 50px 75px 45px !important; } }

.close-modal {
    position: absolute;
    top: 40px;
    right: 50px;
    cursor: pointer;
    path {
        transition: .4s; }
    &:hover path {
        transition: .4s;
        stroke: #2e2e2e; } }

.modal-title {
    font-family: SF UI Display;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    color: #2E2E2E;
    margin-bottom: 50px; }

.feedback-modal-form {
    display: flex;
    flex-wrap: wrap;
    line-height: 18px;
    .modal-title {
        margin-bottom: 50px; }
    input[type="text"] {
        margin-bottom: 25px; } }

label.error {
    display: none !important; }

.thank-text {
    font-size: 18px;
    line-height: 21px;
    color: #2E2E2E; }

#thank-modal {
    width: 560px;
    max-width: calc(100% - 40px) !important;
    max-height: calc(100% - 40px);
    .modal-title {
        margin-bottom: 30px; }
    .iziModal-content {
        padding: 135px 75px !important; } }
