.certif-row {
    list-style: none;
    padding: 0;
    margin: 45px -10px 165px; }

.certif {
    padding: 10px;
    flex: 0 0 25%;
    max-width: 25%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center; }

.certif-img {
    width: auto;
    max-width: 100%;
    height: auto; }

.cert-wrap {
    position: relative;
    cursor: pointer;
    width: 100%;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: rgba(0, 0, 0, 0.8);
        opacity: 0;
        transition: .3s; }
    &:hover {
        .zoom {
            opacity: 1;
            transition: .3s; }
        &:before {
            opacity: 1;
            transition: .3s; } } }

.zoom {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: .3s; }

#cert-modal {
    max-width: calc(100% - 40px) !important;
    max-height: calc(100% - 40px);
    img {
        max-width: 100%; } }
