.reviews {
    list-style: none;
    margin: 45px 0 140px;
    padding: 0; }

.review {
    padding: 25px 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    margin-bottom: 20px;
    transition: .4s;
    &:hover {
        transition: .4s;
        background-color: #F2F5F5;
        .review-img {
            opacity: 1;
            transition: .4s; } } }

.review-content {
    max-width: 920px; }

.review-text {
    font-size: 14px;
    line-height: 17px;
    color: #2E2E2E; }

.review-autor {
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    color: #2E2E2E;
    margin-bottom: 14px; }

.review-brand {
    text-align: center; }

.review-img {
    transition: .4s;
    opacity: .5;
    margin-bottom: 14px; }

.brand-text {
    font-size: 10px;
    line-height: 12px;
    color: #787878; }
