.fotorama {
    margin: 50px 0 110px;
    overflow: visible; }

.fotorama__stage {
    overflow: visible; }

.fotorama__stage__frame {
    transition: transform .3s; }

.fotorama__stage__frame:not(.fotorama__active) {
    transform: scale(.85);
    img {
        opacity: .5; } }

.fotorama__nav__frame img {
    transition: .3s; }

.fotorama__nav__frame:not(.fotorama__active) img {
    opacity: .5; }

.fotorama__thumb-border {
    display: none; }


.fotorama__arr {
    width: 50px;
    height: 50px;
    background: url(../img/slider/arrow.svg) no-repeat;
    transition: .3s;
    opacity: .5;
    &:hover {
        transition: .3s;
        opacity: 1; } }

.fotorama__arr--prev {
    transform: rotate(180deg) !important; }
