.projects-row {
    margin: 50px -10px 110px;
    list-style: none;
    padding: 0; }

.project {
    min-height: 340px;
    flex: 0 0 50%;
    max-width: 50%;
    padding: 10px;
    a {
        position: relative;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        display: block;
        height: 100%;
        width: 100%;
        font-size: 24px;
        line-height: 29px;
        color: #FFFFFF;
        &:hover {
            .project-text {
                opacity: 0;
                transition: .5s; }
            .project-hide-text {
                transition: .5s;
                opacity: 1; } } } }

.project-text {
    position: absolute;
    bottom: 40px;
    left: 50px;
    transition: .5s; }

.project-hide-text {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0,0,0,0.6);
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.35);
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: .5s; }

.project-name {
    margin-bottom: 80px; }

.project-link {
    font-size: 14px;
    display: flex;
    align-items: center;
    img {
        margin-left: 30px; } }
