.about-text {
    overflow: hidden;
    white-space: pre-wrap;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    font-family: inherit;
    margin: 30px 0 70px; }

.consult-block {
    padding: 40px 75px;
    background-color: #F2F5F5;
    margin-bottom: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between; }

.caonsult-content {
    max-width: 705px; }

.consult-title {
    font-size: 24px;
    line-height: 29px;
    color: #2E2E2E;
    margin-bottom: 20px; }

.caonsult-text {
    font-size: 14px;
    line-height: 17px;
    color: #4C4C4C; }

.consult-btn {
    color: #4C4C4C;
    border: 1px solid #D9D9D9; }
