.our_services {
    padding: 50px;
    border-bottom: 1px solid #EAEAEA; }

.our_services_text {
    margin-top: 20px;
    margin-bottom: 50px;
    font-size: 14px;
    line-height: 17px;
    color: #2E2E2E; }


.our_services_item {
    padding: 18px;
    display: flex;
    align-items: center;
    margin-bottom: 32px;
    transition: all .3s;
    text-decoration: none;
    flex: 0 0 50%;
    max-width: 50%;
    &:hover {
        text-decoration: none;
        transition: all .3s;
        background-color: #F7F7F7; } }

.service_img {
    margin-right: 18px;
    max-width: 148px; }

.service_description_header {
    font-size: 18px;
    line-height: 21px;
    color: #000000;
    margin-bottom: 20px; }

.service_description_text {
    font-size: 16px;
    line-height: 19px;
    color: #2E2E2E;
    margin: 0; }
