.trust_us {
    padding-top: 80px;
    padding-bottom: 60px; }

.trust_carousel {
    margin: 40px 0;
    position: relative;
    display: flex;
    justify-content: space-between;
    .owl-stage {
        display: flex;
        align-items: center; }
    .owl-nav {
        height: 0;
        display: block !important; }

    .trust_next,
    .trust_prev {
        position: absolute;
        top: 50%;
        opacity: .5;
        transition: all .3s;
        &:hover {
            opacity: 1;
            transition: all .3s; } }

    .trust_next {
        right: -80px;
        transform: translateY(-50%); }

    .trust_prev {
        left: -80px;
        transform: translateY(-50%) rotate(-180deg); } }

.trust_carousel .owl-item {
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center; }

.trust_carousel .owl-stage .brand-img {
    width: auto;
    opacity: .7;
    cursor: pointer;
    transition: all .4s;
    &:hover {
        transition: all .4s;
        opacity: 1; } }
