.contact-form-title {
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    color: #2E2E2E;
    margin: 50px 0 35px; }

.contact-form {
    margin-bottom: 110px; }

.contacts-container {
    background-color: #F9F9F9;
    margin-top: 40px;
    padding: 50px 0;
    ul {
        padding: 0;
        list-style: none; } }

.contacts {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0; }


.contact-title {
    font-size: 18px;
    line-height: 21px;
    color: #2E2E2E;
    margin-bottom: 22px;
    font-weight: bold; }

.contact-text {
    font-size: 14px;
    line-height: 27px;
    font-weight: 500;
    color: #2E2E2E;
    margin-bottom: 0;
    max-width: 200px; }

.phone-operator {
    font-weight: 500;
    font-size: 9px;
    line-height: 11px;
    color: #C9C9C9;
    margin-left: 10px; }

.contact-form-inputs {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
    input:nth-of-type(2) {
        margin: 0 20px; } }

input[type=submit].contact-input {
    padding: 14px 60px; }

textarea {
    width: 100%;
    height: 160px;
    padding: 15px 20px;
    resize: none;
    &:focus {
        outline: 0;
        box-shadow: 0 0 4px 0 #FF6600; } }
