.footer_contacts {
    border-top: 1px solid #EAEAEA;
    border-bottom: 1px solid #EAEAEA;
    .container {
        position: relative; } }

.contact_info {
    position: absolute;
    width: 580px;
    height: 270px;
    left: 0;
    bottom: 0;
    background: #FFFFFF;
    color: #2E2E2E;
    padding: 50px 75px;
    p {
        font-size: 14px;
        line-height: 17px;
        margin: 0; }
    img {
        margin-right: 15px; } }

.footer_contacts_header {
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 35px; }

.footer_contacts_item {
    display: flex;
    align-items: center;
    margin-bottom: 20px; }

.open_map {
    position: absolute;
    width: 150px;
    height: 150px;
    background: #FF6600;
    top: 50%;
    right: 0;
    transform: translate(50%, -50%);
    padding: 20px 18px;
    text-align: center;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    &:hover {
        color: #FFFFFF; }
    img {
        margin: auto 0 15px auto; }
    p {
        margin: 0 auto;
        display: inline;
        text-align: left; } }

